import { __ } from '@wordpress/i18n';
import { produce } from 'immer';

import { PanelRow, Button, Dashicon, TextControl, ToggleControl } from '@wordpress/components';

import { contentType } from '../../utils/options';
import { icons } from '../../utils/icons';

import { Label, InlineMediaUpload, InlineDetailMediaUpload } from '../../../../Components';
import { BControlPro, SelectControlPro } from '../../../../Components/Pro';
// Item Settings

const ItemSettings = ({ premiumProps, attributes, setAttributes, items, item, index }) => {
    const { lightboxType } = attributes;
    const { type, thumbnail, thumbCaption, content, contentCaption, caption, importCaption } = item;

    console.log({thumbCaption, contentCaption});
    

    const updateItem = (type, index, val, childType = false) => {
        const newItem = produce(items, draft => {
            if (childType) {
                draft[index][type][childType] = val;
            } else {
                draft[index][type] = val;
            }
        });
        setAttributes({ items: newItem });
    }

    /*
     *
     * Remove Item
     * 
    */
    const removeItem = (index) => {
        const removeItem = [...items];
        removeItem.splice(index, 1);
        setAttributes({ items: removeItem });
    }

    /*
     *
     * Item Duplicate 
     * 
    */
    const duplicateItem = (index) => {
        const newitems = [...items.slice(0, index), { ...items[index] }, ...items.slice(index)];
        setAttributes({ items: newitems });
    }

    return <>
        <SelectControlPro label={__('Content Type', 'lightbox')} labelPosition="side" value={type} options={contentType} onChange={val => {
            const newItems = produce(items, draft => {
                draft[index]['type'] = val;
                draft[index]['content'] = 'content' === val ? '<!-- wp:paragraph --><!-- /wp:paragraph -->' : '';
            });
            setAttributes({ items: newItems });
        }} __nextHasNoMarginBottom proValues={['pdf', 'iframe']}  {...premiumProps} />

        {/* Check than add thumbnail or button  */}
        {'image' !== type && 'gallery' === lightboxType && <>
            <Label>{__('Thumbnail:', 'lightbox')}</Label>

            <InlineDetailMediaUpload value={{url:thumbnail, caption:thumbCaption}} types={['image']} onChange={val => {
                 const newItem = produce(items, draft => {
                    draft[index]['thumbnail'] = val?.url;
                    draft[index]['thumbCaption'] = val?.caption || val?.alt || val?.title;
                });
                setAttributes({ items: newItem });
            }} placeholder={__('Enter Image URL', 'lightbox')} />
        </>}

        {/* Image  */}
        {'image' === type && <>
            <Label>{__('Image:', 'lightbox')}</Label>

            <InlineDetailMediaUpload value={{url:content,caption:contentCaption}} types={['image']} onChange={val => { 
                 const newItem = produce(items, draft => {
                    draft[index]['content'] = val?.url;
                    draft[index]['contentCaption'] = val?.caption || val?.alt || val?.title;
                });
                setAttributes({ items: newItem });
             }} placeholder={__('Enter Image URL', 'lightbox')} />
        </>}

        {/* Audio  */}
        {'audio' === type && <>
            <Label>{__('Audio:', 'lightbox')}</Label>
            <InlineMediaUpload value={content} types={['audio']} onChange={val => updateItem('content', index, val)} placeholder={__('Enter Audio URL', 'lightbox')} />
        </>}

        {/* Video  */}
        {'video' === type && <>
            <Label>{__('Video:', 'lightbox')}</Label>
            <InlineMediaUpload value={content} types={['video']} onChange={val => updateItem('content', index, val)} placeholder={__('Enter Video URL', 'lightbox')} />
        </>}

        {/* pdf  */}
        {'pdf' === type && <>
            <Label>{__('Pdf:', 'lightbox')}</Label>
            <InlineMediaUpload value={content} types={['pdf']} onChange={val => updateItem('content', index, val)} placeholder={__('Enter Pdf URL', 'lightbox')} />
        </>}

        {'iframe' === type && <>
            <Label>{__('Iframe:', 'lightbox')}</Label>
            <TextControl value={content} onChange={val => updateItem('content', index, val)} placeholder={__('Iframe Map URL', 'lightbox')} />
            <span>{__('Note:Use only src', 'lightbox')}</span>
        </>}

         {/* caption area  */}
        <BControlPro className='mt10' __nextHasNoMarginBottom label={__('Import Caption From Media', 'lightbox')} checked={ importCaption } onChange={ (val) => {updateItem('importCaption', index, val)} } Component={ToggleControl} {...premiumProps} />

        <Label className='mt10'>{__('Caption', 'lightbox')}</Label>
        <TextControl className="mt10" value={importCaption ? (type == 'image' ? contentCaption : thumbCaption) : caption} onChange={val =>  updateItem('caption', index, val)} />

        {/* Duplicate and Remove Button  */}
        {'button' !== lightboxType && <PanelRow className='itemAction mt20'>
            {1 < items.length && <Button className='removeItem' onClick={() => removeItem(index)} ><Dashicon icon='no' size={18} />{__('Remove', 'lightbox')}</Button>}
            <Button className='duplicateItem' onClick={() => duplicateItem(index)} >{icons.gearSettings}{__('Duplicate', 'lightbox')}</Button>
        </PanelRow>}
    </>
}
export default ItemSettings;